.portrait-wrapper {
  display: contents;
}

.portrait {
  position: relative;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 20px;
  margin: 20px;
  width: 175px;
  max-width: calc(100% - 20px - 20px);
  text-decoration: none;
}

.portrait[data-style="small"] {
  width: 100px;
}

.portrait[data-style="tiny"] {
  flex-direction: row;
  gap: 15px;
  width: unset;
  text-align: left;
}

.portrait-image {
  width: 100%;
  aspect-ratio: 1 / 1;
  border-radius: 999px;
  object-fit: cover;
  box-shadow: var(--shadow);
}

.portrait[data-style="tiny"] .portrait-image {
  width: 50px;
}

.portrait[data-style="tiny"] .portrait-role {
  display: none;
}

.portrait-text {
  display: flex;
  flex-direction: column;
  line-height: calc(var(--spacing) - 0.4);
}

.portrait-name {
  font-family: var(--heading);
  font-weight: var(--semi-bold);
}

.portrait-role .icon {
  position: absolute;
  left: 0;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20%;
  aspect-ratio: 1 / 1;
  border-radius: 999px;
  background: var(--background);
  box-shadow: var(--shadow);
  transform: translate(14%, 14%);
}

.portrait[data-style="small"] .portrait-role .icon {
  left: -2px;
  top: -2px;
}

/* Contents of _sass/_custom-portrait-hover.scss */

.portrait-link {
    /* Set this to relative so the absolutely positioned elements inside are contained */
    position: relative; 
    display: block; 
}

/* Container for the two stacked images */
.portrait-swap-container {
    position: relative; 
    width: 100%;
    height: auto; 
    padding-bottom: 100%; /* Common technique for a square aspect ratio */
}

/* Position both images exactly on top of each other */
.portrait-default,
.portrait-hover {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: opacity 0.3s ease-in-out; 
}

/* Initially hide the hover image */
.portrait-hover {
    opacity: 0;
}

/* When the whole link is hovered over: */
.portrait-link:hover .portrait-default {
    opacity: 0; /* Hide the default image */
}

.portrait-link:hover .portrait-hover {
    opacity: 1; /* Show the hover image */
}